/* popup mode */
html.ple-popup-mode {overflow: hidden;}


.ple-editor {
	position: relative;
	min-height: 480px;
	overflow: hidden;
	//border: 1px solid green;
	*, *:before, *:after {
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}

	&.side-active {
		.ple-container {margin-right: $sidebar-width;}
	}
}


